<template>
  <div class="study page">
    <div class="study-album">
      <div class="study-album-item" v-for="(item, i) in album" :key="i" @click="toDetail(item.href, item.title)">
        <h3>{{item.title}}</h3>
        <p>{{item.desc}}</p>
      </div>
    </div>

    <div class="study-pagination">
      <span ontouchstart="" v-show="this.currentPage < this.totalPages" @click="loadMore">Load more...</span>
    </div>
  </div>
</template>

<script>
import { $fetch } from '@/store'

const DOMAIN = 'https://jrainlau.com/api/study/articles'

export default {
  data () {
    return {
      album: [],
      currentPage: 1
    }
  },
  computed: {
    totalPages () {
      const eg = this.album[0]
      let total = 0
      if (eg) {
        total = eg.totalPages
      }
      return total
    }
  },
  mounted () {
    document.body.querySelector('header').style.display = 'none'
    document.title = 'Study hard'
    this.keyword = localStorage.getItem('study_keyword')
    if (!this.keyword) {
      this.keyword = prompt('Set the keyword')
      this.keyword && localStorage.setItem('study_keyword', this.keyword) && this.getAlbum()
    } else {
      this.getAlbum()
    }
  },
  methods: {
    async getAlbum (index = 1) {
      const result = await $fetch({
        url: DOMAIN,
        method: 'post',
        data: {
          domain: this.keyword,
          index
        }
      }).catch(e => e.response)
      this.album = this.album.concat(result.data)
    },
    loadMore () {
      this.currentPage++
      if (this.currentPage <= this.totalPages) {
        console.log(this.currentPage, this.totalPages)
        this.getAlbum(this.currentPage)
      }
    },
    toDetail (href, title) {
      const newRoute = this.$router.resolve({ path: `/study-article/detail?href=${href}&title=${title}` })
      window.open(newRoute.href, '_blank')
    }
  }
}
</script>

