  height: 100%;
  width: 100%;
  overflow: scroll;
  &-album {
    padding: @gapInner;
    &-item {
      padding-bottom: @gapOuter;
      border-bottom: 1px solid @firstBorderColor;
      h3 {
        margin: 0;
        padding: @gapInner 0;
      }
      p {
        margin: 0;
        color: @monorFontColor;
      }
    }
  }
  &-pagination {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: @gapOuter;
    span {
      display: inline-block;
      margin: @gapOuter;
      padding: @gapInner;
      border: 1px solid @firstBorderColor;
      cursor: pointer;
      &:active {
        background: #5c007a;
        border-color: #5c007a;
        color: #fff;
      }
    }
  }
}

@media only screen and (max-width: 450px) {
  .study-album {
    padding-top: calc(@gapInner + 70px);
  }
}
</style>
